import React from 'react';
import { Box } from '@mui/material';
import NewsBox from './NewsBox'; // Make sure to import the NewsBox component

const newsData: NewsData[] = [
    {
        title: 'SILOVAO TASTU ZVONOM ZA SURENJE SVINJA',
        imageUrl: 'data:image/jpeg;base64,',
        authorName: "Jovan Jovanovic"
    },
    {
        title: 'SURIO TASTU ZVONOM ZA SILOVANJE SVINJA',
        imageUrl: 'data:image/png;base64,',
        authorName: "Jovan Jovic"
    },
    // Add more news items as needed
  ];


interface NewsData {
  title: string;
  imageUrl: string;
  authorName: string;
}

const NewsContainer: React.FC = () => {


    return (
        <div>
            <Box display="flex" flexDirection="column" alignItems="center">
            {newsData.map((newsItem, index) => (
                <NewsBox key={index} authorName={newsItem.authorName} title={newsItem.title} imageUrl={newsItem.imageUrl} />
            ))}
            </Box>
        </div>
    );
};

export default NewsContainer;
